@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@700&display=swap');

body {
  font-family: 'Josefin Sans', sans-serif;
  background-color: #161722;
  margin: 0;

  .App {
    display: flex;
    flex-direction: column;
    justify-content: center; }

  .App-header {
    background-image: url('./Components/images/bg-desktop-dark.jpg');
    background-repeat: no-repeat;
    background-size: cover; }

  .toDoHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 30%;
    margin: 1rem auto;

    h1 {
      color: white;
      font-size: 2.5rem;
      letter-spacing: 30px; } }

  .todoInputContainer {
    margin: 1rem auto;
    width: 30%;
    position: relative;

    .inputTodo {
      background-color: hsl(237, 14%, 26%);
      width: 100%;
      border: none;
      outline: none;
      color: hsl(234, 39%, 85%);
      height: 2.5rem;
      border-radius: 5px;
      padding: 0.2rem;
      font-size: 1.1rem;
      padding-left: 2.5rem;
      box-shadow: 0px 13px 21px -1px rgba(0,0,0,0.49);
      transition: 300ms;
      &:hover {
        box-shadow: 0px 13px 21px 0px #1a108ef9; } }

    .circle {
      color: #5f5f5f;
      position: absolute;
      left: 15px;
      top: 15px; } }

  .newTask {
    display: flex;
    flex-direction: column;
    width: 50%;
    color: hsl(234, 39%, 85%);
    font-size: 1.1rem;
    background-color: hsl(237, 14%, 26%);
    border-radius: 7px;
    margin: 1rem auto;
    box-shadow: 0px 13px 21px -1px rgba(0,0,0,0.49); }

  .todosContainer {
    display: flex;
    align-items: center;
    padding: 0.2rem;
    border-bottom: 1px solid #575572;
    padding-left: 2.5rem; }

  .task {
    display: flex;
    align-items: center;
    width: 100%;
    i {
      color: #5f5f5f;
      position: relative;
      right: 20px;
      cursor: pointer; } }

  .taskCompleted {
    background-color: green;
    cursor: pointer;
    position: relative;
    right: 21px;
    border-radius: 50%;
    padding: 2px;
    width: 16px; }

  .todoItemCheck {
    text-decoration: line-through; }

  .deleteBtn {
    cursor: pointer;
    position: relative;
    right: 20px; }

  .filtersComponents {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 46%;
    color: hsl(234, 39%, 85%);
    font-size: 1.1rem;
    border-bottom: 1px solid rgb (57, 58, 75);
    background-color: hsl(237, 14%, 26%);
    border-radius: 3px;
    margin: auto;
    padding: 0 2rem;

    .filter-button-blue {
      border: none;
      background: none;
      color: #8488ff;
      cursor: pointer;
      transition: 300ms;
      &:hover {
        color: white; } }

    .filter-button-gray {
      border: none;
      background: none;
      color: #5f5f5f;
      cursor: pointer;
      transition: 300ms;
      &:hover {
        color: white; } } } }

@media screen and (max-width: 625px) {
  body {

    .toDoHeader {
      margin: auto 4rem; }

    .newTask {
      width: 90%;
      font-size: 1rem; }

    .todoInputContainer {
      width: 78%;
      margin: 1rem; } }

  .filtersSection {
    .filtersComponents {
      width: 78%;
      color: hsl(234, 39%, 85%);
      font-size: 0.9rem; } } }
